import React from "react";
import Countdown from "react-countdown";
import TotalStaked from "./A_TotalStaked";
import { Form } from "react-bootstrap";

const StakingCard = (props) => {
  console.info("stakingcard", props);
  return (
    <>
      <TotalStaked />
      <section className="staking-card">
        <div className="card-header">
          <h3>REWARD </h3>
        </div>
        <div className="card-body">
          <section className="card-input">
            <p>HOLDER REWARD(ALL-TIME)</p>
            <Form.Control style={{ marginBottom: "20px" }} />
            <p>HOLDER REWARD (24H)</p>
            <Form.Control style={{ marginBottom: "20px" }} />
          </section>
          <div className="card-percentbuttons ">
            <button class="stake-small-button">25%</button>
            <button class="stake-small-button">50%</button>
            <button class="stake-small-button">75%</button>
            <button class="stake-small-button">100%</button>
          </div>
          <section className="card-counterdown">
            <div className="card-counterdown-time">
              <Countdown date={Date.now() + 500000000} />
            </div>
            <div className="card-counterdown-unit">
              <strong>
                DD
              </strong>
              <strong>
                HR
              </strong>
              <strong>
                MIN
              </strong>
              <strong>
                SEC
              </strong>
            </div>
          </section>
        </div>
        <button
          class="poolcard-button"
          style={{ textAlign: "center", marginTop: "20px" }}
        >
          JOIN POOL
        </button>
      </section>
    </>
  );
};

export default StakingCard;
