import React, { useState } from "react";
import { useWeb3React } from "@web3-react/core";

import { useModal } from "../contexts/modalContext";
import config from "../contexts/modalConfig";
import metamask from "../components/icons/metamask.svg";

import logo from "../assets/img/logo.png";
import { BsThreeDotsVertical, FaAlignJustify } from "react-icons/all";
import { Link } from "react-router-dom";
import useAuth from "./../hooks/useAuth";
import Button from "@mui/material/Button";

import {Navbar, Container, Nav, NavDropdown, Form, FormControl} from 'react-bootstrap';

import { connectorLocalStorageKey } from "../config";

const Header = () => {
  const { account } = useWeb3React();
  const { login, logout } = useAuth();
  const [state, setState] = useState({
    right: false,
    isOpen: false,
  });
  const [isOpen, setIsOpen] = useState(false);
  const handleToggle = () => {
    setIsOpen(true);
  };
  const { setModalTitle, setModalBody, unSetModal } = useModal();
  const accountElipsis = account
    ? `${account.substring(0, 4)}...${account.substring(account.length - 4)}`
    : null;
  const walletConnect = () => {
    if (account) {
      unSetModal();
      logout();
      window.localStorage.removeItem(connectorLocalStorageKey);
    } else {
      unSetModal();
      login();
      window.localStorage.setItem(connectorLocalStorageKey, "injected");
    }
  };

  const toggleDrawer = (anchor, open) => (event) => {
    console.info(anchor, open, event);
    if (
      event &&
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, right: open });
  };

  const WalletButtons = () => {
    return (
      <div style={{ display: "flex", flexDirection: "column" }}>
        {config.map((entry, index) => (
          <Button
            key={entry.title}
            onClick={walletConnect}
            style={{ justifyContent: "space-around" }}
          >
            <img src={metamask} alt="meta" style={{ width: "60px" }} />
            {entry.title}
          </Button>
        ))}
      </div>
    );
  };

  return (
    <section className="header">
      <nav className="navbar">
        <Link to="/">
          <img
            src={logo}
            alt="logo"
            style={{ height: "100%", maxHeight: "50px" }}
          ></img>
        </Link>
        <div className="nav-center">
          <ul className={isOpen ? "nav-links show-nav" : "nav-links"}>
            <li>
              <Link to="/projects">IDO PROJECTS</Link>
            </li>
            <li>
              <Link to="/staking">STAKING</Link>
            </li>
            <li>
              <Link to="/vesting">VESTING</Link>
            </li>
            <li>
              <Link to="/applyido">APPLY FOR IDO</Link>
            </li>
            {account ? (
              <li
                onClick={() => {
                  setModalTitle("Logout");
                  setModalBody(
                    <div
                      style={{
                        display: "flex",
                        flexDirection: "column",
                        textAlign: "center",
                      }}
                    >
                      <h4>{account}</h4>
                      <Button onClick={walletConnect}>Logout</Button>
                    </div>
                  );
                }}
                className="connect-button"
              >
                {accountElipsis}
              </li>
            ) : (
              <li
                onClick={() => {
                  setModalTitle("Connect Wallet");
                  setModalBody(<WalletButtons />);
                }}
                className="connect-button"
              >
                CONNECT
              </li>
            )}

            <li
              type="button"
              data-bs-toggle="offcanvas"
              data-bs-target="#offcanvasRight"
              aria-controls="offcanvasRight"
            >
              <BsThreeDotsVertical
                onClick={toggleDrawer("right", true)}
                size={30}
                style={{ marginLeft: 0, marginTop: "7px", cursor: "pointer" }}
              />
            </li>
          </ul>
          <ul className="nav-mobile">
            <li>
              <button
                type="button"
                className="nav-btn"
                onClick={handleToggle}
              >
                <FaAlignJustify className="nav-icon" />
              </button>
            </li>            
          </ul>
        </div>
      </nav>

      <div
        className="offcanvas offcanvas-end"
        tabIndex="-1"
        id="offcanvasRight"
        aria-labelledby="offcanvasRightLabel"
      >
        <div className="offcanvas-header">
          <h1 id="offcanvasRightLabel">IDO</h1>
          <button
            type="button"
            className="btn-close text-reset"
            data-bs-dismiss="offcanvas"
            aria-label="Close"
          ></button>
        </div>
        <div className="offcanvas-body p-lg-0">
          <ul className="navbar-nav">
            <li>
              <Link to="/projects">IDO PROJECTS</Link>
            </li>
            <li>
              <Link to="/staking">STAKING</Link>
            </li>
            <li>
              <Link to="/vesting">VESTING</Link>
            </li>
            <li>
              <Link to="/applyido">APPLY IDO</Link>
            </li>
          </ul>
        </div>
      </div>

      {/* <div className="navbar">

      <Navbar bg="light" expand={false} >
        <Container fluid>
          <Navbar.Brand href="#">Navbar Offcanvas</Navbar.Brand>
          <Navbar.Toggle aria-controls="offcanvasNavbar" />
          {/* <Navbar.Offcanvas
            id="offcanvasNavbar"
            aria-labelledby="offcanvasNavbarLabel"
            placement="end"
          >
            <Offcanvas.Header closeButton>
              <Offcanvas.Title id="offcanvasNavbarLabel">
                Offcanvas
              </Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body>
              <Nav className="justify-content-end flex-grow-1 pe-3">
                <Nav.Link href="#action1">Home</Nav.Link>
                <Nav.Link href="#action2">Link</Nav.Link>
                <NavDropdown title="Dropdown" id="offcanvasNavbarDropdown">
                  <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
                  <NavDropdown.Item href="#action4">
                    Another action
                  </NavDropdown.Item>
                  <NavDropdown.Divider />
                  <NavDropdown.Item href="#action5">
                    Something else here
                  </NavDropdown.Item>
                </NavDropdown>
              </Nav>
              <Form className="d-flex">
                <FormControl
                  type="search"
                  placeholder="Search"
                  className="me-2"
                  aria-label="Search"
                />
                <Button variant="outline-success">Search</Button>
              </Form>
            </Offcanvas.Body>
          </Navbar.Offcanvas>
        </Container>
      </Navbar>
      </div> */}

    </section>
  );
};

export default Header;
