import React, { Component } from "react";
import { Container, Row, Col, Table, Card } from "react-bootstrap";
import Fade from "react-reveal/Fade";
import Zoom from "react-reveal/Zoom";

import FeatureCard from "./FeatureCard";

import { features } from "../../assets/variables";
import leftsidebigmamba from "../../assets/img/leftside_Big_mamba.png";
import doughnut from "../../assets/img/doughnut.png";

const MainFeatures = () => {
  return (
    <>
      <section className="container feature">
        <section className="feature" >
          <Zoom left>
            <div className="feature-mamba">
              <img src={leftsidebigmamba} alt="leftsidebigmamba" />
            </div>
          </Zoom>
          <Fade left>
            <h3 className="col-12 text-center font-weight-bold sub-title">
              MAIN FEATURES
            </h3>
            <p className="text-center">
              Quam a at nisl, vulputate blandit etiam. Lacinia cras enim sit
              viverra imperdiet. Id malesuada eget scelerisque elit.
            </p>
          </Fade>
          <section className="projects">
            {features.map((feature, index) => {
              return (
                  <FeatureCard key={index} feature={feature} />
              );
            })}
          </section>
        </section>
        <section>
          <Zoom cascade>
            <Card className="tokenomics">
              <Row>
                <Col lg md="6">
                  <h3 className="col-12 text-center font-weight-bold feature-title">
                    MAIN FEATURES
                  </h3>
                  <p className="text-center">
                    Quam a at nisl, vulputate blandit etiam. Lacinia cras enim
                    sit viverra imperdiet. Id malesuada eget scelerisque elit.
                  </p>
                  <Table
                    bordered
                    hover
                    variant="dark"
                    style={{ marginLeft: "10px", borderColor: "orange" }}
                  >
                    <thead>
                      <tr>
                        <th>First Name</th>
                        <th>Last Name</th>
                        <th>Username</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>Mark</td>
                        <td>Otto</td>
                        <td>@mdo</td>
                      </tr>
                      <tr>
                        <td>Jacob</td>
                        <td>Thornton</td>
                        <td>@fat</td>
                      </tr>
                      <tr>
                        <td>Jacob</td>
                        <td>Thornton</td>
                        <td>@fat</td>
                      </tr>
                    </tbody>
                  </Table>
                </Col>
                <Col lg md="6" style={{ marginTop: "30px" }}>
                  <img src={doughnut} alt="doughnut" />
                </Col>
              </Row>
              <Row style={{ marginTop: "30px" }}>
                <Table
                  bordered
                  hover
                  variant="dark"
                  style={{ marginLeft: "25px", marginRight: "25px" }}
                >
                  <thead>
                    <tr>
                      <th>First Name</th>
                      <th>Last Name</th>
                      <th>Username</th>
                      <th>First Name</th>
                      <th>Last Name</th>
                      <th>Username</th>
                      <th>Username</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>Mark</td>
                      <td>Otto</td>
                      <td>@mdo</td>
                      <td>Mark</td>
                      <td>Otto</td>
                      <td>@mdo</td>
                      <td>@mdo</td>
                    </tr>
                    <tr>
                      <td>Jacob</td>
                      <td>Thornton</td>
                      <td>@fat</td>
                      <td>Thornton</td>
                      <td>@fat</td>
                      <td>Thornton</td>
                      <td>@fat</td>
                    </tr>
                    <tr>
                      <td>Jacob</td>
                      <td>Thornton</td>
                      <td>@fat</td>
                      <td>Thornton</td>
                      <td>@fat</td>
                      <td>Thornton</td>
                      <td>@fat</td>
                    </tr>
                  </tbody>
                </Table>
              </Row>
            </Card>
          </Zoom>
        </section>
      </section>
      <hr style={{ borderColor: "#f1b90c" }} />
    </>
  );
};

export default MainFeatures;